import styles from "../styles/Navigator.module.css";

const Navigator = () => {
	return (
		<div className={styles.container}>
			<a href={`${process.env.PUBLIC_URL}`}>ABOUT</a>
			<a href={`${process.env.PUBLIC_URL}/xml`}>XML → XLSX</a>
			<a href={`${process.env.PUBLIC_URL}/xlsx`}>XLSX → XML</a>
			<a href={`${process.env.PUBLIC_URL}/publish`}>⛔PUBLISH🛠</a>
		</div>
	);
};

export default Navigator;
